:host {
  display: block; }

.segment[data-tab="all-namedFragments"] table tr.added {
  background-color: #D9EEFF; }

.segment[data-tab="all-namedFragments"] table tr.modified {
  background-color: #FFFBB4; }

.segment[data-tab="all-namedFragments"] table tr, .segment[data-tab="all-namedFragments"] table td {
  cursor: pointer; }

.confirm-deletion .description {
